.burger {
    width: 27px;
    height: 18px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    position: absolute;
    top: 4.5%;
    right: 10%;

    &__line {
        display: block;
        width: 100%;
        height: 2px;
        background: #000;
    }
}